.format {
  padding: 10rem 0;

  &__container {
    display: flex;
    gap: 2rem;
    max-width: calc(192rem + var(--container-offset) * 2);
    padding: 0;
  }

  &__content {
    flex-basis: 66.1rem;
    flex-grow: 1;
    background-color: var(--pl-white);
    padding: 9.8rem 10.1rem 9rem;
  }
  &__title {
    margin-bottom: 3.9rem;
    line-height: 100%;
  }

  &__desc {
    font-weight: 500;
    font-size: 2.8rem;
    line-height: 130%;
    ol,
    ul {
      margin: 0;
      list-style: none;

      li:not(:last-child) {
        margin-bottom: 2.3rem;
      }
    }
    ul {
      padding-left: 0;
    }
    ol {
      padding-left: 0;
      counter-reset: counter;
      li {
        counter-increment: counter;
        display: flex;
        align-items: flex-start;
        gap: 2rem;
        &::before {
          content: counter(counter);
          color: var(--pl-blue);
        }
      }
    }

    p {
      margin: 0;
    }
  }
  &__img {
    flex-grow: 1;
    flex-basis: calc(100% - 66.1rem - 2rem);
    flex-shrink: 0;
    min-height: 57.4rem;

    position: relative;

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }
}
